import styled from 'styled-components';

export const TopCurrencyWrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background: rgba(230, 167, 231, 0.28);
  padding: 4px 8px;
  border-radius: 4px;
  margin: 4px 4px;
  width: 160px;
  font-size: 13px;
  transition: all 0.3s;

  @media screen and (max-width: 850px) {
    flex-direction: row;
    width: 210px;
    align-items: center;
    padding: 0 4px;
  }
`